import * as u from './utils';

export const {
    stream,
    scan,
    nestComponent
} = u;

export default function initialize(createApp, render) {
    // UPDATE
    let update = stream();

    // APP
    let app = createApp(update);

    // INITIAL MODEL
    let initialModel = app.model();

    // MODELS
    // -- callback (argument of update function), initialmodel, stream
    let models = scan((model, cb) => cb(model), initialModel, update);

    // CONNECT RENDER TO STREAMS
    models.map(render(app));

    // WATCH THE MODEL IN THE CONSOLE
    models.map((...args) => console.log(...args));

    // INITIALIZE APP
    models(initialModel);
}



